import * as yup from 'yup';
export const yupValidation = yup.object().shape({
  name: yup
    .string()
    .min(2, 'mnimo 2 caracteres')
    .required('Nome  obrigatrio'),
  email: yup.string().email('email invlido').required('Email  obrigatrio'),
  telephone: yup
    .string()
    .matches(/\(?\d{0,3}\)?\s?\d{4,5}-\d{4}/g, 'telefone invlido')
    .required('Telefone  obrigatrio'),
  cpf: yup
    .string()
    .matches(/\d{3}\.\d{3}\.\d{3}-\d{2}/g, 'Cpf invlido')
    .max(14, 'Cpf invlido')
    .required('Cpf  obrigatrio'),
  city: yup
    .array()
    .min(1, 'Cidade  obrigatria')
    .required('Cidade  obrigatria'),
  country: yup
    .array()
    .min(1, 'Pas  obrigatrio')
    .required('Pas  obrigatrio'),
});
